import { fetchPopularMoviesByCast } from "@/lib/fetchData";
import Image from "next/image";

export default async function CastKnownFor({ castId }: { castId: number }) {
  const knownFor = await fetchPopularMoviesByCast(castId);
  const poster = "https://image.tmdb.org/t/p/w185";
  console.log(knownFor);
  return (
    <div>
      <h1>Known For</h1>
      <div className="flex">
        {knownFor.map((item) => {
          return (
            <div key={item.id}>
              <img src={poster + item.poster_path} />
            </div>
          );
        })}
      </div>
    </div>
  );
}
